import React, {useEffect, useState} from 'react'
import {Alert, Button, FloatingLabel, Form, FormLabel, InputGroup} from 'react-bootstrap';
import {changeBit, getBit} from '../../../service/bitmask';
import {inRange} from '../../../service/checker';
import axios from 'axios';
import {ready} from '../../../service/recaptcha';
import {API_PREFIX} from "../../../service/apiUrl";

function UpdateScode() {
    const [grade, setGrade] = useState(1);
    const [clas, setClas] = useState();
    const [scode, setScode] = useState();
    const [working, setWorking] = useState(true);
    const [workState, setWorkState] = useState(-1);
    const [formState, setFormState] = useState(0);

    const yrs = new Date().getFullYear();

    useEffect(() => {
        axios.get(API_PREFIX+'/user/api/scode-change/query')
            .then((res: any) => {
                setGrade(res.data['result']);
                setWorking(false);
            })
            .catch(err => {
                window.location.reload();
            });
    }, []);

    function submit() {
        let state = 0;
        if (!inRange(1, 4, clas)) state = changeBit(state, 0);
        if (!inRange(1, 24, scode)) state = changeBit(state, 1);
        setFormState(state);
        if (state !== 0) return;

        setWorking(true);
        ready('update_scode', token => {
            axios.patch(API_PREFIX+'/user/api/scode-change/change', {
                clas: clas,
                scode: scode,
                ctoken: token
            })
                .then(() => {
                    window.location.reload();
                })
                .catch(err => {
                    switch (err.response?.data['result']) {
                        case 1:
                        case 6:
                        case 4:
                            setWorkState(1);
                            break;
                        case 2:
                            setWorkState(2);
                            break;
                        case 3:
                            setWorkState(3);
                            break;
                        case 5:
                            setWorkState(4);
                            break;
                    }
                })
                .finally(() => {
                    setWorking(false);
                });
        });
    }

    return (
        <form className='vstack gap-3 d-flex justify-content-center align-items-center text-center form-signin'>
            <h1 className='h3 my-3 fw-normal'>IonID</h1>
            <p className='fw-bold fs-5 m-1'> .</p>
            <InputGroup>
                <FloatingLabel label=''>
                    <Form.Control type='number'
                                  className={'pe-5 fs-6 form-control-lg' + (getBit(formState, 0) ? ' is-invalid' : '')}
                                  disabled={working}
                                  placeholder=''
                                  aria-label=''
                                  value={clas}
                                  onChange={e => setClas(Number.parseInt(e.target.value))}
                    />
                    <FormLabel htmlFor='clas'></FormLabel>
                </FloatingLabel>
                <FloatingLabel label=''>
                    <Form.Control type='number'
                                  className={'pe-5 fs-6 form-control-lg' + (getBit(formState, 1) ? ' is-invalid' : '')}
                                  disabled={working}
                                  placeholder=''
                                  aria-label=''
                                  value={scode}
                                  onChange={e => setScode(Number.parseInt(e.target.value))}
                    />
                    <Form.Label htmlFor='scode'></Form.Label>
                </FloatingLabel>
            </InputGroup>
            {inRange(1, 3, grade) && inRange(1, 4, clas) && inRange(1, 24, scode) &&
                <div className='vstack'>
                    <p className='my-0'>{yrs - 1992 - grade} {'' + grade + clas + (scode < 10 ? '0' + scode : scode)}.</p>
                </div>
            }
            <Button className='mt-2' disabled={working} onClick={submit}>Continue</Button>
            {workState === 1 &&
                <Alert variant='danger'>
                    <p className='m-1'>  .</p>
                    <p className='m-1'>  .</p>
                </Alert>
            }
            {workState === 2 &&
                <Alert variant='danger'>
                    <p className='m-1'>reCAPTCHA  .</p>
                </Alert>
            }
            {workState === 3 &&
                <Alert variant='danger'>
                    <p className='m-1'>       .</p>
                </Alert>
            }
            {workState === 4 &&
                <Alert variant='danger'>
                    <p className='m-1'> IonID    .</p>
                </Alert>
            }
            <p className='m-0 muted'>    .</p>
        </form>
    )
}

export default UpdateScode;