import React, {useState} from "react";
import {Alert, Button, ButtonGroup, Row} from "react-bootstrap";
import axios from "axios";
import {API_PREFIX} from "../../../service/apiUrl";

function ChangeNsPreset(props) {
    const [working, setWorking] = useState<boolean>(false);
    const [workState, setWorkState] = useState<number>(0);

    function exec(mode) {
        setWorking(true);
        axios.patch(API_PREFIX+'/manage/api/ns/mode/set', {
            mode: mode
        })
            .then(res => {
                props.updatePreset(mode);
            })
            .catch(err => {
                switch (err.response?.data['result']) {
                    case 1:
                        setWorkState(1);
                        break;
                    case 2:
                        setWorkState(2);
                        break;
                    case 3:
                        setWorkState(3);
                        break;
                    default:
                        setWorkState(-1);
                        break;
                }
            })
            .finally(() => {
                setWorking(false);
            });
    }

    return (
        <Row className="my-3">
            <h2 className="mb-3">   </h2>
            <ButtonGroup className='w-fit mb-2'>
                <Button disabled={working} onClick={() => exec(0)}
                        className='w-fit'>3 </Button>
                <Button disabled={working} onClick={() => exec(1)}
                        className='w-fit'>4 </Button>
            </ButtonGroup>
            <div>
                <p className={'my-2'}>    : {props.timePreset === 0 ? "3" : "4"}</p>
                {props.timePreset === 0 &&
                    <ul>
                        <li>8</li>
                        <li>1</li>
                        <li>2</li>
                    </ul>
                }
                {props.timePreset === 1 &&
                    <ul>
                        <li> 1</li>
                        <li> 2</li>
                        <li> 1</li>
                        <li> 2</li>
                    </ul>
                }
            </div>
            {workState === 1 &&
                <Alert variant='danger w-fit'> .</Alert>
            }
            {workState === 2 &&
                <Alert variant='danger w-fit'> .</Alert>
            }
            {workState === 3 &&
                <Alert variant='danger w-fit'>      .</Alert>
            }
            {workState === -1 &&
                <Alert variant='danger w-fit'>    .</Alert>
            }
        </Row>
    );
}

export default ChangeNsPreset;