import axios from 'axios';
import React, {useState} from 'react'
import {Button, Form, Modal, Spinner, Stack} from 'react-bootstrap';
import {API_PREFIX} from "../../../service/apiUrl";

function Promote() {
    const [confirmModalShow, setConfirmModalShow] = useState(false);
    const [workState, setWorkState] = useState(-1);
    const [challengeN, setChallengeN] = useState(-1);
    const [userChallenge, setUserChallenge] = useState(0);
    const [working, setWorking] = useState(false);

    function startPromote() {
        setConfirmModalShow(true);
        setWorkState(-1);
        setUserChallenge(0);
        setChallengeN(Math.round(Math.random() * 100) + 10)
    }

    function closeConfirmModal() {
        setConfirmModalShow(false);
    }

    function beginWork() {
        if (challengeN * (challengeN + 1) !== userChallenge * 2) {
            setWorkState(1);
            return;
        }
        setWorking(true);
        axios.patch(API_PREFIX+'/manage/api/bulk/promote')
            .then(res => {
                setWorkState(2);
            })
            .catch(err => {
                setWorkState(3);
            })
            .finally(() => {
                setWorking(false);
            });
    }

    return (
        <>
            <Stack>
                <p className='m-1'>  </p>
                <ul className='m-1'>
                    <li> 1, 2   1 .</li>
                    <li> 1, 2     .</li>
                    <li> 3  IonID .</li>
                </ul>
                <Button variant='outline-primary w-fit my-2' onClick={startPromote}> </Button>
            </Stack>
            <Modal show={confirmModalShow} dialogClassName='modal-dialog-centered'>
                <Modal.Header>
                    <Modal.Title></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {!working &&
                        <>
                            {(workState === -1 || workState === 1) &&
                                <>
                                    <p className='m-1'>  .  </p>
                                    <ul className='m-1'>
                                        <li> 1, 2   1 .</li>
                                        <li> 1, 2     .</li>
                                        <li> 3  IonID .</li>
                                    </ul>
                                    <p className='m-1'>   1  N  {challengeN}   N  .</p>
                                    <Form.Control
                                        type='number'
                                        value={userChallenge}
                                        placeholder=''
                                        onChange={e => setUserChallenge(Number.parseInt(e.target.value))}
                                    />
                                    {workState === 1 &&
                                        <p className='m-1 text-danger'> .</p>
                                    }
                                </>
                            }
                            {workState === 2 &&
                                <div className='d-flex flex-column align-items-center'>
                                    <p className='my-1'>  .</p>
                                </div>
                            }
                            {workState === 3 &&
                                <div className='d-flex flex-column align-items-center'>
                                    <p className='my-1'>   .</p>
                                    <p className='my-1'>  .</p>
                                </div>
                            }
                        </>
                    }
                    {working &&
                        <div className='d-flex flex-column align-items-center'>
                            <Spinner animation='grow'/>
                            <p className='my-3'>.</p>
                        </div>
                    }
                </Modal.Body>
                {!working &&
                    <Modal.Footer>
                        {(workState === -1 || workState === 1) &&
                            <>
                                <Button onClick={beginWork}></Button>
                                <Button onClick={closeConfirmModal}></Button>
                            </>
                        }
                        {(workState === 2 || workState === 3) &&
                            <>
                                <Button onClick={closeConfirmModal}></Button>
                            </>
                        }
                    </Modal.Footer>
                }
            </Modal>
        </>
    );
}

export default Promote;