import React from 'react';
import {Link} from 'react-router-dom';

function ErrorPage(props) {
    return (
        <>
            <main
                className="container p-4 d-flex justify-content-center align-items-center flex-column text-center h-100 w-100">
                <Link to='/'><img src="/static/image/logo.png" width="60%" className="my-3" alt=''/></Link>
                <hr/>
                <h1 className="mt-4"> .</h1>
                <p className='fs-4'>{props.exp}</p>
            </main>
        </>
    )
}

export default ErrorPage;