import React, {useState} from "react";
import {Alert, Button, Col, Form, InputGroup, Row} from "react-bootstrap";
import {changeBit, getBit} from "../../../service/bitmask";
import axios from "axios";
import {inRange} from "../../../service/checker";
import {API_PREFIX} from "../../../service/apiUrl";

function AddNs(props) {
    const [revTime, setRevTime] = useState(-1);
    const [revPlace, setRevPlace] = useState('');
    const [revRes, setRevRes] = useState('');
    const [formState, setFormState] = useState(0);

    const [working, setWorking] = useState(false);
    const [workError, setWorkError] = useState(-1);

    const scode = props.scode;
    const setScode = props.setScode;
    const timePreset = props.timePreset;

    function exe() {
        let state = 0

        // validate
        if (!(
            timePreset == 0 && (
                revTime == 0 || revTime == 1 || revTime == 2
            ) ||
            timePreset == 1 && (
                revTime == 3 || revTime == 4 || revTime == 5 || revTime == 6
            )
        )) state = changeBit(state, 0);
        if (!inRange(1, 30, revPlace.length)) state = changeBit(state, 1);
        if (!inRange(1, 30, revRes.length)) state = changeBit(state, 3);

        setFormState(state);

        if (state !== 0) return;

        setWorking(true);
        axios.post(API_PREFIX+'/manage/api/ns/create', {
            scode: scode,
            time: revTime,
            place: revPlace,
            reason: revRes
        })
            .then(res => {
                setWorkError(0);
            })
            .catch(err => {
                const es = err.response?.data['result'];
                switch (es) {
                    case 1:
                        setWorkError(1);
                        break;
                    case 2:
                        setWorkError(2);
                        break;
                    case 3:
                        setWorkError(3);
                        break;
                    case 4:
                        setWorkError(4);
                        break;
                    default:
                        setWorkError(6);
                }
            })
            .finally(() => {
                setWorking(false);
            });
    }

    return (
        <Row className="my-3">
            <h2 className="mb-3">  </h2>
            <div>
                <Row className='mt-1'>
                    <Form.Group as={Col} className='mb-3'>
                        <Form.Label htmlFor='time' className='form-label'></Form.Label>
                        <Form.Select isInvalid={getBit(formState, 0) === 1} aria-label=' ' disabled={working}
                                     value={revTime} onChange={e => setRevTime(Number.parseInt(e.target.value))}>
                            <option value={-1}> </option>
                            {timePreset === 0 &&
                                <>
                                    <option value={0}>8</option>
                                    <option value={1}>1</option>
                                    <option value={2}>2</option>
                                </>
                            }
                            {timePreset === 1 &&
                                <>
                                    <option value={3}> 1</option>
                                    <option value={4}> 2</option>
                                    <option value={5}> 1</option>
                                    <option value={6}> 2</option>
                                </>
                            }
                        </Form.Select>
                    </Form.Group>
                    <Form.Group as={Col} className='mb-3'>
                        <Form.Label htmlFor='place' className='form-label'></Form.Label>
                        <Form.Control
                            type='text'
                            isInvalid={getBit(formState, 1) === 1}
                            value={revPlace}
                            disabled={working}
                            onChange={e => setRevPlace(e.target.value)}
                        />
                    </Form.Group>
                    <Form.Group as={Col} className='mb-3'>
                        <Form.Label htmlFor='reason' className='form-label'></Form.Label>
                        <Form.Control
                            type='text'
                            isInvalid={getBit(formState, 3) === 1}
                            value={revRes}
                            disabled={working}
                            onChange={e => setRevRes(e.target.value)}
                        />
                    </Form.Group>
                </Row>
                <Row>
                    <Form.Group as={Col} className="mb-3">
                        <InputGroup className='w-25 mgw'>
                            <InputGroup.Text></InputGroup.Text>
                            <Form.Control
                                type='number'
                                placeholder=''
                                value={scode}
                                onChange={e => setScode(Number.parseInt(e.target.value))}
                            />
                            <Button onClick={exe}></Button>
                        </InputGroup>
                    </Form.Group>
                    <p>        .     .</p>
                    {workError === 0 &&
                        <Alert variant="success">  .</Alert>
                    }
                    {workError === 1 &&
                        <Alert variant="danger"> .</Alert>
                    }
                    {workError === 2 &&
                        <Alert variant="danger">  .</Alert>
                    }
                    {workError === 3 &&
                        <Alert variant="danger"> IonID .</Alert>
                    }
                    {workError === 4 &&
                        <Alert variant="danger">      .</Alert>
                    }
                    {workError === 6 &&
                        <Alert variant="danger"> .</Alert>
                    }
                </Row>
            </div>
        </Row>
    )
}

export default AddNs;