import React, {useEffect, useState} from "react";
import {Alert, Button, FloatingLabel, Form, Stack} from "react-bootstrap";
import axios from "axios";
import {ready} from "../../../service/recaptcha";
import ErrorPage from "../../etc/error";
import {API_PREFIX} from "../../../service/apiUrl";

function IForgotDashboard(props) {
    const [privateCode, setPrivateCode] = useState<string>('');
    const [workState, setWorkState] = useState<number>(-1);

    useEffect(() => {
        axios.get(API_PREFIX+"/auth/api/reset-passwd/query", {
            params: {id: props.id}
        }).then(res => {
            if (res.data['result']['status'] === 'REQUESTED') {
                setPrivateCode(res.data['result']['privateCode']);
                setWorkState(0);
            } else {
                setWorkState(1);
            }
        }).catch(err => {
            setWorkState(2);
        });
    }, []);

    if (workState === 2) {
        <ErrorPage exp=' .'/>
    }

    return (
        <main className='container mt-4'>
            <Stack className='justify-content-center align-items-center text-center'>
                <h1 className='h3 mt-3 mb-0 fw-normal'>IonID </h1>
                <p className={'h5 my-4 fw-normal'}> </p>
                <p className={'my-1'}>  .        .    .</p>
                <p className={'my-1'}>        .       .</p>
                <p className={'my-1'}>          .   .</p>
                {workState === -1 &&
                    <p className={'fs-5 fw-bold'}></p>
                }
                {workState === 0 &&
                    <>
                        <p className={'fs-4 fw-bold mt-4 mb-1'}>  : {privateCode}</p>
                        <p className={'fs-5 fw-bold'}>      .</p>
                    </>
                }
                {workState === 1 &&
                    <p className={'fs-5 fw-bold'}>    .</p>
                }
            </Stack>
        </main>
    )
}

function IForgot() {
    const [stage, setStage] = React.useState<number>(0);
    const [workState, setWorkState] = useState<number>(-1);
    const [working, setWorking] = useState<boolean>(false);

    const [name, setName] = useState<string>('');
    const [scode, setScode] = useState<number>();
    const [id, setId] = useState<string>('');

    function nxt() {
        setWorking(true);
        axios.get(API_PREFIX+'/auth/api/reset-passwd/verify', {
            params: {id: id}
        }).then(res => {
            switch (res.data['result']) {
                case 0:
                    setStage(1);
                    setWorkState(-1);
                    break;
                case 1:
                    setWorkState(3);
                    break;
                case 2:
                    setWorkState(0);
                    break;
                case 3:
                    setStage(2);
                    setWorkState(-1);
                    break;
            }
        }).catch(err => {
            setWorkState(1);
        }).finally(() => {
            setWorking(false);
        });
    }

    function submit() {
        setWorking(true);
        ready('reset_password_request', function (token: string) {
            axios.post(API_PREFIX+'/auth/api/reset-passwd/request', {
                id: id,
                name: name,
                scode: scode,
                ctoken: token
            }).then(res => {
                setStage(2);
                setWorkState(-1);
            }).catch(err => {
                switch (err?.response.data['result']) {
                    case 1:
                        setWorkState(3);
                        break;
                    case 3:
                        setWorkState(4);
                        break;
                    case 4:
                        setWorkState(5);
                        break;
                    case 5:
                    case 6:
                        setWorkState(6);
                        break;
                    case 7:
                    default:
                        setStage(2);
                }
            }).finally(() => {
                setWorking(false);
            });
        });
    }

    if (stage === 2) {
        return (
            <IForgotDashboard id={id}/>
        );
    }

    return (
        <main className='container mt-4'>
            <form className='vstack gap-3 d-flex justify-content-center align-items-center text-center form-signin'>
                <h1 className='h3 mt-3 mb-0 fw-normal'>IonID </h1>
                <p className={'h5 fw-normal'}> </p>
                {stage === 0 &&
                    <>
                        <p className='fs-6 m-0'> IonID .</p>
                        <div>
                            <FloatingLabel label={'IonID'}>
                                <Form.Control type={'text'}
                                              placeholder={'IonID'}
                                              autoComplete={'username'}
                                              disabled={(stage !== 0) || working}
                                              value={id}
                                              onChange={e => setId(e.target.value)}
                                              onKeyDown={e => {
                                                  if (e.key === 'Enter') nxt();
                                              }}
                                />
                            </FloatingLabel>
                        </div>
                    </>
                }
                {stage === 1 &&
                    <>
                        <p className='fs-6 m-0'> IonID   .</p>
                        <div>
                            <FloatingLabel label={''} className={'my-3'}>
                                <Form.Control type={'text'}
                                              placeholder={''}
                                              autoComplete={'name'}
                                              disabled={working}
                                              value={name}
                                              onChange={e => setName(e.target.value)}
                                              onKeyDown={e => {
                                                  if (e.key === 'Enter') submit();
                                              }}
                                />
                            </FloatingLabel>
                            <FloatingLabel label={''} className={'my-1'}>
                                <Form.Control type={'number'}
                                              placeholder={''}
                                              disabled={working}
                                              value={scode}
                                              onChange={e => setScode(parseInt(e.target.value))}
                                              onKeyDown={e => {
                                                  if (e.key === 'Enter') submit();
                                              }}
                                />
                            </FloatingLabel>
                        </div>
                        <Button className={'w-fit'} onClick={submit}></Button>
                    </>
                }
                {stage === 0 &&
                    <Button className={'w-fit'} onClick={nxt}></Button>
                }
                {workState === 0 &&
                    <Alert variant={'danger'}>  IonID .</Alert>
                }
                {workState === 1 &&
                    <Alert variant={'danger'}>IonID  .</Alert>
                }
                {workState === 2 &&
                    <Alert variant={'danger'}>   .</Alert>
                }
                {workState === 3 &&
                    <Alert variant={'danger'}>      .</Alert>
                }
                {workState === 4 &&
                    <Alert variant={'danger'}>reCAPTCHA  .</Alert>
                }
                {workState === 5 &&
                    <Alert variant={'danger'}>        .</Alert>
                }
                {workState === 6 &&
                    <Alert variant={'danger'}>IonID   .</Alert>
                }
            </form>
        </main>
    );
}

export default IForgot;