import React, {useState} from "react";
import {Alert, Button, Col, Form, InputGroup, Modal, Row, Table} from "react-bootstrap";
import NsState from "../../ns/nsState";
import axios from "axios";
import {API_PREFIX} from "../../../service/apiUrl";

function QueryNs(props) {
    const [workError, setWorkError] = useState(-1);
    const [nsLst, setNsLst] = useState<any[]>([]);

    const [targetNs, setTargetNs] = useState([]);
    const [deleteModalShow, setDeleteModalShow] = useState(false);
    const [deleting, setDeleting] = useState(false);
    const [deleteResult, setDeleteResult] = useState(-1);

    const scode = props.scode;
    const setScode = props.setScode;

    function exe() {
        axios.get(API_PREFIX+'/manage/api/ns/get-user', {
            params: {code: scode}
        })
            .then(res => {
                setNsLst(res.data['result']['reqs']);
                setWorkError(0);
            })
            .catch(err => {
                const cd = err.response?.data['result'];
                switch (cd) {
                    case 1:
                        setWorkError(1);
                        break;
                    case 2:
                        setWorkError(2);
                        break;
                    default:
                        setWorkError(3);
                        break;
                }
            });
    }

    let rr: any[] = [];
    if (!workError) {
        if (nsLst.length === 0) {
            rr.push(
                <tr>
                    <td colSpan={7}>
                        <p className='my-2 fw-bold'>   .</p>
                    </td>
                </tr>
            );
        } else {
            nsLst.forEach(req => {
                let row = <NsState
                    time={req.time}
                    place={req.place}
                    superviser={req.supervisor}
                    reason={req.reason}
                    seat={req.lnsSeat}
                    lnsReq={req.lnsReq}
                    status={req.status}
                    showDeleteConfirm={() => setDeleteModalShow(true)}
                    setTargetNs={setTargetNs}
                />
                rr.push(row);
            });
        }
    } else {
        rr.push(
            <tr>
                <td className='table-danger' colSpan={7}>     .</td>
            </tr>
        )
    }

    function deleteNs() {
        setDeleting(true);
        axios.delete(API_PREFIX+'/manage/api/ns/delete', {
            params: {
                code: scode,
                time: targetNs[0]
            }
        })
            .then(res => {
                closeDeleteConfirm();
                exe();
            })
            .catch(err => {
                setDeleteResult(1);
            })
            .finally(() => {
                setDeleting(false);
            });
    }

    function closeDeleteConfirm() {
        setDeleteModalShow(false);
        setDeleteResult(-1);
    }

    return (
        <>
            <Row className='my-3'>
                <h2 className="mb-3">  </h2>
                <Form.Group as={Col}>
                    <InputGroup className='w-25 mb-3 mgw'>
                        <InputGroup.Text></InputGroup.Text>
                        <Form.Control
                            type='number'
                            placeholder=''
                            value={scode}
                            onChange={e => setScode(Number.parseInt(e.target.value))}
                        />
                        <Button onClick={exe}></Button>
                    </InputGroup>
                    {workError === 1 &&
                        <Alert variant='danger w-fit'> .</Alert>
                    }
                    {workError === 2 &&
                        <Alert variant='danger w-fit'> IonID .</Alert>
                    }
                    {workError === 3 &&
                        <Alert variant='danger w-fit'> .</Alert>
                    }
                    {workError === 0 &&
                        <div className='table-cover'>
                            <Table className='m-auto'>
                                <thead>
                                <tr>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th> </th>
                                    <th></th>
                                    <th></th>
                                </tr>
                                </thead>
                                <tbody>{rr}</tbody>
                            </Table>
                        </div>
                    }
                </Form.Group>
            </Row>
            <Modal show={deleteModalShow} onHide={closeDeleteConfirm} dialogClassName='modal-dialog-centered'>
                <Modal.Header closeButton>
                    <Modal.Title>   </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {deleteResult === -1 &&
                        <p>{scode} {targetNs[1]}     ?</p>
                    }
                    {deleteResult === 1 &&
                        <p className='bg-danger text-white p-3 rounded'> .</p>
                    }
                </Modal.Body>
                <Modal.Footer>
                    {deleteResult === -1 &&
                        <>
                            <Button onClick={deleteNs} disabled={deleting}></Button>
                            <Button onClick={closeDeleteConfirm} disabled={deleting}></Button>
                        </>
                    }
                    {deleteResult === 0 &&
                        <>
                            <Button onClick={closeDeleteConfirm}></Button>
                        </>
                    }
                    {deleteResult === 1 &&
                        <>
                            <Button onClick={deleteNs} disabled={deleting}></Button>
                            <Button onClick={closeDeleteConfirm} disabled={deleting}></Button>
                        </>
                    }
                </Modal.Footer>
            </Modal>
        </>
    )
}

export default QueryNs;