import React, {useEffect, useState} from 'react';
import jsPDF from "jspdf";
import "jspdf-autotable";
import {
    Alert,
    Button,
    Container,
    Form,
    FormCheck,
    FormGroup,
    FormSelect,
    InputGroup,
    Row,
    Stack,
    Table
} from 'react-bootstrap';
import axios from 'axios';
import {changeBit, getBit} from "../../../service/bitmask";
import {API_PREFIX} from "../../../service/apiUrl";

function RecordRow(props) {
    function copy(str: string) {
        navigator.clipboard.writeText(str);
    }

    const data = props.data;

    if(!props.has || (!props.includeDenied && !data.a)) {
        return (
            <td></td>
        )
    }

    return (
        <>
            { props.includeDenied &&
                <td className={data.a ? 'table-success' : 'table-danger'}>
                    <a onClick={() => copy(data.c)}>{data.c}</a>
                </td>
            }
            { (!props.includeDenied && data.a) &&
                <td>
                    <a title={'Copy'} className={'ns-lst-cpy'} onClick={() => copy(data.c)}>{data.c}</a>
                </td>
            }
        </>
    )
}

function PrintNs(props) {
    const [data, setData] = useState<any[]>([]);
    const [grade, setGrade] = useState<number>(1);
    const [date, setDate] = useState<string>('');
    const [includeDenied, setIncludeDenied] = useState<boolean>(false);
    const [workState, setWorkState] = useState<number>(-1);
    const [filterByClass, setFilterByClass] = useState<number>(15);

    function exportPdf() {
        axios.get(API_PREFIX+'/static/font/SpoqaHanSansNeo-normal.b64')
            .then(res => {
                exportPdfAfter(res);
            })
            .catch(err => {
                console.error(err);
                setWorkState(3);
            });
    }

    function exportPdfAfter(font: string) {
        var doc = new jsPDF('portrait', 'mm', 'a4');

        doc.addFileToVFS("SpoqaHanSansNeo.ttf", font);
        doc.addFont("SpoqaHanSansNeo.ttf", "SpoqaHanSansNeo", "normal");
        doc.setFont("SpoqaHanSansNeo");

        doc.setFontSize(18);
        doc.text('  ', 10, 13);
        doc.setFontSize(10);
        doc.text(date, 10, 18);

        doc.setFontSize(10);
        doc.autoTable({
            html: '#prt',
            headStyles: {halign: "center", valign: "middle", fillColor: [59, 59, 59]},
            tableLineColor: [59, 59, 59],
            tableLineWidth: 0.1,
            startX: 10,
            startY: 20,
            margin: {left: 10, top: 10, right: 10, bottom: 10},
            styles: {font: "SpoqaHanSansNeo", fontStyle: "normal"},
        });

        doc.save(`   ${date}.pdf`);
    }

    const timePreset = props.timePreset;

    function createTable() {
        axios.get(API_PREFIX+'/manage/api/ns/print', {
            params: {grade: grade}
        })
            .then(res => {
                setData(res.data['result']['ns']);
                setDate(res.data['result']['qtime']);
                setWorkState(0);
            })
            .catch(err => {
                switch (err.response?.data['result']) {
                    case 1:
                        setWorkState(1);
                        break;
                    default:
                        setWorkState(2);
                }
            });
    }

    let rr: any[] = [];
    if (workState === 0) {
        data.forEach(e => {
            let clas = (e.code-e.code%100)/100-10*(grade);
            if(getBit(filterByClass, clas-1) === 0) return;
            rr.push(
                <tr>
                    <td>{e.code}</td>
                    <td>{e.name}</td>
                    {timePreset === 0 &&
                        <>
                            <RecordRow has={e.hasOwnProperty("0")} data={e['0']} includeDenied={includeDenied}/>
                            <RecordRow has={e.hasOwnProperty("1")} data={e['1']} includeDenied={includeDenied}/>
                            <RecordRow has={e.hasOwnProperty("2")} data={e['2']} includeDenied={includeDenied}/>
                        </>
                    }
                    {timePreset === 1 &&
                        <>
                            <RecordRow has={e.hasOwnProperty("3")} data={e['3']} includeDenied={includeDenied}/>
                            <RecordRow has={e.hasOwnProperty("4")} data={e['4']} includeDenied={includeDenied}/>
                            <RecordRow has={e.hasOwnProperty("5")} data={e['5']} includeDenied={includeDenied}/>
                            <RecordRow has={e.hasOwnProperty("6")} data={e['6']} includeDenied={includeDenied}/>
                        </>
                    }
                </tr>
            );
        });
    }

    useEffect(() => {
        let c = localStorage.getItem('nlfc');
        if(c === null) {
            localStorage.setItem('nlfc', 15);
            setFilterByClass(15);
        } else {
            setFilterByClass(Number.parseInt(c));
        }

        let g = localStorage.getItem('nlg');
        if(g === null) {
            localStorage.setItem('nlg', '1');
            setGrade(1);
        }
        else {
            setGrade(Number.parseInt(g));
        }
    }, []);
    function updateFilterByClass(clas: number) {
        let c = 0;
        c = changeBit(filterByClass, clas);
        setFilterByClass(c);
        localStorage.setItem('nlfc', c);
    }
    function updateGrade(grade: number) {
        setGrade(Number.parseInt(grade));
        localStorage.setItem("nlg", grade);
    }

    return (
        <Row className='my-3'>
            <h2>  </h2>
            <FormGroup>
                <InputGroup className='w-25 mgw'>
                    <FormSelect value={grade} onChange={e => updateGrade(e.target.value)}>
                        <option value={1}>1</option>
                        <option value={2}>2</option>
                        <option value={3}>3</option>
                    </FormSelect>
                    <Button onClick={createTable}></Button>
                </InputGroup>
                <Form.Check
                    label='   '
                    className='mt-1'
                    id='drq'
                    checked={includeDenied}
                    onChange={e => setIncludeDenied(e.target.checked)}
                />
                <Stack direction={'horizontal'} gap={3}>
                    <FormCheck
                        label={'1'}
                        id={'clas-1'}
                        checked={getBit(filterByClass, 0) === 1}
                        onChange={() => updateFilterByClass(0)}
                    />
                    <FormCheck
                        label={'2'}
                        id={'clas-2'}
                        checked={getBit(filterByClass, 1) === 1}
                        onChange={() => updateFilterByClass(1)}
                    />
                    <FormCheck
                        label={'3'}
                        id={'clas-3'}
                        checked={getBit(filterByClass, 2) === 1}
                        onChange={() => updateFilterByClass(2)}
                    />
                    <FormCheck
                        label={'4'}
                        id={'clas-4'}
                        checked={getBit(filterByClass, 3) === 1}
                        onChange={() => updateFilterByClass(3)}
                    />
                </Stack>
            </FormGroup>
            <Container className='my-3'>
                {workState === 0 &&
                    <div className='table-cover'>
                        <Table id='prt'>
                            <thead>
                            <tr>
                                <th></th>
                                <th></th>
                                { timePreset === 0 &&
                                    <>
                                        <th>8</th>
                                        <th>1</th>
                                        <th>2</th>
                                    </>
                                }
                                { timePreset === 1 &&
                                    <>
                                        <th> 1</th>
                                        <th> 2</th>
                                        <th> 1</th>
                                        <th> 2</th>
                                    </>
                                }
                            </tr>
                            </thead>
                            <tbody>{rr}</tbody>
                        </Table>
                        <p>{date}</p>
                    </div>
                }
                {workState === 1 &&
                    <Alert variant='danger'> .</Alert>
                }
                {workState === 2 &&
                    <Alert variant='danger'> .</Alert>
                }
                {workState === 3 &&
                    <Alert variant='danger'>PDF  .</Alert>
                }
            </Container>
        </Row>
    );
}

export default PrintNs;
