import React, {useState} from 'react'
import {Alert, Button, FloatingLabel, Form, Stack} from 'react-bootstrap';
import {changeBit, getBit} from '../../../service/bitmask';
import {inRange} from '../../../service/checker';
import axios from 'axios';
import {ready} from '../../../service/recaptcha';
import {useNavigate} from "react-router-dom";
import {API_PREFIX} from "../../../service/apiUrl";

function ResetPassword() {
    const [privateCode, setPrivateCode] = useState<string>('');
    const [stage, setStage] = useState<number>(0);
    const [working, setWorking] = useState<boolean>(false);
    const [workState, setWorkState] = useState<number>(-1);
    const [newPassword, setNewPassword] = useState<string>('');
    const [checkPassword, setCheckPassword] = useState<string>('');
    const [formState, setFormState] = useState<number>(0);

    const urlParams = new URLSearchParams(window.location.search);
    const tokenUrl = urlParams.get('token');

    function nxt() {
        setWorking(true);
        ready('check_private_code', token => {
            axios.post(API_PREFIX+'/auth/api/reset-passwd/check-private', {
                token: tokenUrl,
                privateCode: privateCode,
                ctoken: token
            }).then(res => {
                setStage(1);
            }).catch(err => {
                switch (err?.response.data['result']) {
                    case 1:
                    case 2:
                        setWorkState(1);
                        break;
                    case 3:
                        setWorkState(2);
                        break;
                    case 4:
                        setWorkState(3);
                        break;
                    case 5:
                        setWorkState(5);
                        break;
                    case 6:
                        setWorkState(6);
                        break;
                    default:
                        setWorkState(4);
                }
            }).finally(() => {
                setWorking(false);
            });
        });
    }

    const navigate = useNavigate();

    function submit() {
        let state = 0;
        if (!inRange(6, 100, newPassword.length)) state = changeBit(state, 0);
        if (newPassword !== checkPassword) state = changeBit(state, 1);

        setFormState(state);
        if (state !== 0) return;

        setWorking(true);
        ready("reset_password", token => {
            axios.patch(API_PREFIX+'/auth/api/reset-passwd/reset', {
                pwd: newPassword,
                token: tokenUrl,
                ctoken: token
            }).then(res => {
                navigate('/');
            }).catch(err => {
                switch (err?.response.data['result']) {
                    case 4:
                        setWorkState(5);
                        break;
                    case 5:
                        setWorkState(6);
                        break;
                    case 3:
                        setWorkState(7);
                        break;
                    case 2:
                        setWorkState(8);
                        break;
                    case 1:
                    case 6:
                    case 7:
                    default:
                        setWorkState(4);
                }
            }).finally(() => {
                setWorking(false);
            });
        });
    }

    return (
        <form className='vstack gap-3 d-flex justify-content-center align-items-center text-center form-signin'>
            <h1 className='h3 mt-3 mb-0 fw-normal'>IonID </h1>
            <p className={'h5 fw-normal'}> </p>
            {stage === 0 &&
                <>
                    <FloatingLabel label='  '>
                        <Form.Control type={'text'}
                                      placeholder={'  '}
                                      value={privateCode}
                                      onChange={e => setPrivateCode(e.target.value)}
                                      onKeyDown={e => {
                                          if (e.key === 'Enter') nxt();
                                      }}
                                      disabled={working}
                        />
                    </FloatingLabel>
                    <Button onClick={nxt}></Button>
                    {workState === 1 && <Alert variant={'danger'}> .</Alert>}
                    {workState === 2 && <Alert variant={'danger'}>   .</Alert>}
                    {workState === 3 && <Alert variant={'danger'}>  .</Alert>}
                    {workState === 4 && <Alert variant={'danger'}>   .</Alert>}
                    {workState === 5 && <Alert variant={'danger'}>reCAPTCHA  .</Alert>}
                    {workState === 6 && <Alert variant={'danger'}>       .</Alert>}
                </>
            }
            {stage === 1 &&
                <Stack className="gap-3 justify-content-center align-items-center">
                    <FloatingLabel label='New Password'>
                        <Form.Control type={'password'}
                                      placeholder={'New Password'}
                                      className={'pe-5 fs-6' + (getBit(formState, 0) ? ' is-invalid' : '')}
                                      value={newPassword}
                                      onChange={e => setNewPassword(e.target.value)}
                                      onKeyDown={e => {
                                          if (e.key === 'Enter') submit();
                                      }}
                                      autoComplete={'new-password'}
                                      disabled={working}
                        />
                        <p className='invalid-feedback mb-0'> 6  .</p>
                    </FloatingLabel>
                    <FloatingLabel label='Password Confirm'>
                        <Form.Control type={'password'}
                                      placeholder={'Confirm Password'}
                                      className={'pe-5 fs-6' + (getBit(formState, 1) ? ' is-invalid' : '')}
                                      value={checkPassword}
                                      onChange={e => setCheckPassword(e.target.value)}
                                      onKeyDown={e => {
                                          if (e.key === 'Enter') submit();
                                      }}
                                      autoComplete={'new-password'}
                                      disabled={working}
                        />
                        <p className='invalid-feedback mb-0'> .</p>
                    </FloatingLabel>
                    <Button onClick={submit}></Button>
                    {workState === 4 && <Alert variant={'danger'}>   .</Alert>}
                    {workState === 7 && <Alert variant={'danger'}> 6  .</Alert>}
                    {workState === 8 && <Alert variant={'danger'}>   ( ).</Alert>}
                    {workState === 5 && <Alert variant={'danger'}>reCAPTCHA  .</Alert>}
                    {workState === 6 && <Alert variant={'danger'}>       .</Alert>}
                </Stack>
            }
        </form>
    )
}

export default ResetPassword;