import axios from 'axios';
import React, {useState} from 'react'
import {Alert, Button, FormControl, InputGroup} from 'react-bootstrap';
import {API_PREFIX} from "../../../service/apiUrl";

function RemoveGrade() {
    const [ionId, setIonId] = useState('');
    const [workState, setWorkState] = useState(-1);

    function exe() {
        axios.patch(API_PREFIX+'/manage/api/ionid/eliminate', {
            id: ionId
        })
            .then(res => {
                setWorkState(0);
            })
            .catch(err => {
                switch (err.response?.data['result']) {
                    case 1:
                        setWorkState(1);
                        break;
                    case 2:
                        setWorkState(2);
                        break;
                    default:
                        setWorkState(3);
                }
            });
    }

    return (
        <>
            <InputGroup className='w-25 mgw'>
                <InputGroup.Text>IonID</InputGroup.Text>
                <FormControl type='text' placeholder='IonID' value={ionId} onChange={e => setIonId(e.target.value)}/>
                <Button onClick={exe}></Button>
            </InputGroup>
            <p className='my-1'>   IonID , ,       .</p>
            <p className='my-1 fst-italic fw-bold'>  .</p>
            {workState === 0 &&
                <Alert variant='success'>// .</Alert>
            }
            {workState === 1 &&
                <Alert variant='danger'> .</Alert>
            }
            {workState === 2 &&
                <Alert variant='danger'> IonID ..</Alert>
            }
            {workState === 3 &&
                <Alert variant='danger'> .</Alert>
            }
        </>
    );
}

export default RemoveGrade;