import React from 'react';
import Dactivation from './archive/d_activation';
import Deula from './archive/d_eula';
import Dcredit from './archive/d_credit';
import {Container} from 'react-bootstrap';
import {Link, useParams} from 'react-router-dom';
import Dintro from './archive/d_intro';
import Dsystem from "./archive/d_system";

const DOC_REGISTRY = {
    'activation': ['IonID ', (<Dactivation/>)],
    'eula': ["", (<Deula/>)],
    'introduction': ["Ion ", (<Dintro/>)],
    'credits': ["Project Ion", <Dcredit/>],
    'system': ["Ion System", <Dsystem/>]
};

function DocsMain() {
    return (
        <Container className='nst'>
            <div className="p-5 mb-4 bg-light rounded-3">
                <div className="container-fluid py-5">
                    <h1 className="display-5 fw-bold">Ion </h1>
                    <p className="col-md-8 fs-4">Ion     .</p>
                    <Link className="btn btn-primary btn-lg" to="/docs/introduction">Go</Link>
                </div>
            </div>
            <div className="row align-items-md-stretch">
                <div className="col-md-6 mb-3">
                    <div className="h-100 p-5 text-white bg-dark rounded-3">
                        <h2>Ion  </h2>
                        <p>Ion   .    Ion       .</p>
                        <Link className="btn btn-outline-light" to="/docs/eula">Ion  </Link>
                    </div>
                </div>
                <div className="col-md-6 mb-3">
                    <div className="h-100 p-5 bg-light border rounded-3">
                        <h2>Ion  </h2>
                        <p>Ion       .  ,   .      
                            .</p>
                        <Link className="btn btn-outline-dark" to='/docs/credits'>Ion Credits</Link>
                    </div>
                </div>
            </div>
        </Container>
    );
}

function NoDocs() {
    return (
        <Container className='nst my-5 text-center'>
            <h1>  .</h1>
            <Link className='text-decoration-none fs-2' to="/docs"> </Link>
        </Container>
    );
}

function Docs() {
    let param = useParams()['*'];

    if (param === '') {
        return (
            <DocsMain/>
        );
    }
    if (!DOC_REGISTRY.hasOwnProperty(param)) {
        return (
            <NoDocs/>
        )
    }

    return (
        <>
            <Link to='/docs' className='text-decoration-none text-black'>
                <h1>{DOC_REGISTRY[param][0]}</h1>
            </Link>
            <hr/>
            <Container className='nst docs'>
                {DOC_REGISTRY[param][1]}
            </Container>
        </>
    )
}

export default Docs;